.contacts {
  background-color: $bg-second;

  &--bg-color {
    background-color: $standard-color;
  }

  @media (min-width: $desktop-width) {
    &--bg-color {
      background-color: $bg-second;
    }
  }
}

.contacts__container {
  @media (min-width: $desktop-width) {
    position: relative;
    z-index: 1;
  }
}

.contacts__wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 28px;
  padding-bottom: 27px;

  @media (min-width: $tablet-width) {
    padding-top: 65px;
    padding-bottom: 58px;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    top: 95px;
    left: $wrapper-desk-left;
    padding-right: 44px;
    padding-left: 35px;
    background-color: $standard-color;
  }
}

.contacts__title {
  min-width: 140px;

  @include font-oswald(16px, 20px);
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    width: 160px;
    margin-left: 41px;
    font-size: 20px;
    line-height: 25px;
  }

  @media (min-width: $desktop-width) {
    margin-right: 90px;
  }
}

.contacts__address {
  @include font-arial(14px, 20px);
  font-style: normal;

  @media (min-width: $tablet-width) {
    width: 160px;
    margin-right: 41px;
    text-align: right;
    font-size: 16px;
  }

  @media (min-width: $desktop-width) {
    text-align: left;
  }
}

.contacts__address-span {
  display: block;

  @media (min-width: $tablet-width) {
    margin-top: 18px;
  }
}

.contacts__map {
  height: 361px;
  overflow: hidden;
  background-color: $bg-second;
  background-image: url("../img/bg-map-mobile@1x.png");
  background-repeat: no-repeat;
  background-position: bottom;
  background-clip: border-box;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-map-mobile@2x.png");
  }

  @media (min-width: $tablet-width) {
    height: 396px;
    background-image: url("../img/bg-map-tablet@1x.png");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-map-tablet@2x.png");
    }
  }

  @media (min-width: $desktop-width) {
    height: 400px;
    background-image: url("../img/bg-map-desktop@1x.png");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-map-desktop@2x.png");
    }
  }
}

.contacts__iframe {
  width: 100%;
  height: 362px;
  border: none;

  @media (min-width: $tablet-width) {
    height: 400px;
  }
}
